@import "root.css";

header {
  position: fixed;

  width: 100%;
  height: 200px;
  text-align: right;
}

h1 {
  color: var(--purple);
  font-family: 'Chewy', cursive;
  font-weight: lighter;
  font-size: 50px;
  margin: 20px;
}

.who-to-follow {
  padding: 1rem;
  float: right;
  background: radial-gradient(100% 98.4% at 0% 1.8%, rgba(195, 188, 240, 0.5) 0%, rgba(173, 240, 255, 0.5) 48.44%, rgba(245, 209, 167, 0.5) 100%);
  border-radius: 20px;
  line-height: 30px;
  position: fixed;
  top: 9em;
  right: 20px;
  width: 15rem;
  min-height: 15rem;
}

.trending {
  padding: 1rem;
  float: right;
  background: radial-gradient(100% 98.4% at 0% 1.8%, rgba(195, 188, 240, 0.5) 0%, rgba(173, 240, 255, 0.5) 48.44%, rgba(245, 209, 167, 0.5) 100%);
  border-radius: 20px;
  line-height: 30px;
  position: fixed;
  top: 30em;
  right: 20px;
  width: 15rem;
  min-height: 15rem;
}

@media screen and (max-width:1100px) {
  .who-to-follow {
    visibility: hidden;
  }

  .trending {
    visibility: hidden;
  }

}

nav {
  width: 15%;
  height: 100vh;
  margin: 10px;
  border-radius: 20px;
  background-color: #ffffff;
  position: fixed;
  top: 0;
}

h3 {
  margin-bottom: 15px;
}

.feed {
  padding-top: 144px;
}